import { NextRequest, NextResponse } from 'next/server';
import { getEndpoint, getUsageStats, getPaymentsForEndpoint } from '@/db/metered-endpoints';

/**
 * GET /api/v1/endpoints/[endpointId]/stats
 * 
 * Get endpoint statistics and analytics
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  try {
    const { endpointId } = await params; // Next.js 15+ requires awaiting params
    const { searchParams } = new URL(request.url);
    
    const startDate = searchParams.get('startDate') || undefined;
    const endDate = searchParams.get('endDate') || undefined;
    const limit = parseInt(searchParams.get('limit') || '100');

    const endpoint = await getEndpoint(endpointId);

    if (!endpoint) {
      return NextResponse.json(
        {
          error: 'Endpoint not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }

    // Get usage stats
    const stats = await getUsageStats(endpointId, startDate, endDate);

    // Get recent payments
    const payments = await getPaymentsForEndpoint(endpointId, limit);

    return NextResponse.json({
      success: true,
      endpointId: endpoint.id,
      endpointName: endpoint.name,
      period: {
        startDate: startDate || null,
        endDate: endDate || null,
      },
      stats: {
        ...stats,
        totalRevenue: payments.reduce((sum, p) => sum + BigInt(p.amount), BigInt(0)).toString(),
      },
      recentPayments: payments,
    });
  } catch (error) {
    console.error('[endpoints] Stats error:', error);
    return NextResponse.json(
      {
        error: 'Failed to get stats',
        code: 'INTERNAL_ERROR',
        details: error instanceof Error ? error.message : 'Unknown error',
      },
      { status: 500 }
    );
  }
}

